.b-about-list {
    list-style:none;
}

.b-about-list__item {
    position: relative;
}
.b-about-list__item:before {
    content: '';
    background-image: url(data:image/svg+xml;base64,);
    position: absolute;
    background-repeat: no-repeat;
    background-position: 0px 5px;
    left: -1.4rem;
    width: 0.9375rem;
    height: 1.25rem;
    display: inline-block;
}
